.projectCard {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 5px 10px;
  margin: 0 5px 5px 5px;
  background-color: var(--palette_bg);
  border: 1px solid var(--palette_border);
  border-radius: 3px;
}

.projectCard:active {
  background-color: var(--palette_activeBg);
  color: var(--palette_activeTextProductCard);
  & a,
  & a:visited {
    color: (var(--palette_activeTextProductCard));
  }
}

.clickableArea {
  width: 100%;
  cursor: pointer;
}
